@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:flex;flex-direction:column;height:100vh;font-family:'Roboto', sans-serif}.App .header-container{height:7vh;display:flex;box-sizing:border-box;justify-content:space-between;align-items:center;padding:5px}.App .header-container.light{background:#71d6fa}.App .header-container.dark{background:#2C5364;color:#FFFFFF}.App .header-container.dark .ant-btn{background:#2b5060 !important;color:#FFFFFF}.App .home-container{display:flex;flex-grow:1;justify-content:space-between;align-items:center;flex-direction:column}.App .home-container.light{background:#2980B9;background:linear-gradient(to top, #fff, #6DD5FA, #2980B9)}.App .home-container.light .ant-btn{background:#71d6fa !important}.App .home-container.dark{background:#0F2027;background:linear-gradient(to top, #2C5364, #203A43, #0F2027);color:#FFFFFF}.App .home-container.dark .ant-btn{background:#2b5060 !important;color:#FFFFFF}.App .home-container .search-container{height:60px;width:50vw;padding:10px}.App .home-container .search-bar{width:100%}.App .home-container .weather-container{flex-grow:1;display:flex;flex-direction:column;width:80%;border:1px solid black;border-radius:5px;padding:0 5%;margin-bottom:5px}.App .home-container .weather-top{display:flex;justify-content:space-between;align-items:center;height:20%}.App .home-container .weather-top-right{display:flex;align-items:center}.App .home-container .weather-top-right .favourite-icon{font-size:30px;margin-right:10px}.App .home-container .weather-top-right .favourite-btn{width:180px}.App .home-container .weather-middle{display:flex;height:40%;justify-content:center}.App .home-container .weather-middle .marquee{height:100%;display:flex;flex-direction:column;justify-content:space-around;position:relative}.App .home-container .weather-middle .marquee-icon{width:17vw}.App .home-container .weather-middle .marquee-loader{font-size:50px}.App .home-container .weather-middle .marquee-text{width:100%;text-align:center;font-size:2rem;position:absolute;bottom:15%;line-height:1}.App .home-container .weather-bottom{height:40%}.App .favourites-body{display:flex;flex-grow:1;padding:5%}.App .favourites-body.light{background:#2980B9;background:linear-gradient(to top, #fff, #6DD5FA, #2980B9)}.App .favourites-body.light .ant-btn{background:#71d6fa !important}.App .favourites-body.dark{background:#0F2027;background:linear-gradient(to top, #2C5364, #203A43, #0F2027);color:#FFFFFF}.App .favourites-container{display:flex;flex-grow:1}.App .spreadout{display:flex;justify-content:space-evenly;height:100%;width:100%}.App .card-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;height:60%;max-height:140px;flex-grow:1;max-width:120px}.App .card-container.light{background:whitesmoke}.App .card-container.dark{background:#0F2027}.App .clickable{cursor:pointer}@media (min-width: 600px){.App .card-container{width:10vw;border:1px solid grey;border-radius:5px;box-shadow:4px 6px 10px -1px rgba(0,0,0,0.13)}.App .header-title{transform:translate(-50%)}}@media (max-width: 600px){.App .card-container{width:15vw}.App .card-container:not(:first-child){border-left:1px solid grey}.App .favourite-btn{display:none}.App .marquee-icon{width:50vw !important}}.App .light,.App .dark{transition:all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);transition-property:all;transition-duration:0.3s;transition-timing-function:cubic-bezier(0.645, 0.045, 0.355, 1);transition-delay:0s}

